.form {
    
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    width: 300px;
}
.form__title {
    text-align: center;
    margin: 30px;
}
.form__label {
    display: block;
    margin-left: auto;
    margin-right: auto;
  
  }
  .form__input {
  margin-bottom: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 15px 28px 16px 28px;
  background-color: transparent;
  border: 2px solid #ff0081;
  box-sizing: border-box;
  color: grey;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.08em;
  outline: #813a5a;
  cursor: pointer;
  }
  .form__button {
    display: inline-block;
    align-items: center;
    margin-top: 50px;
    width: 50%;
    text-align: center;
    position: relative;
    appearance: none;
    background: #ff0081;
    padding: 1em 2em;
    border: none;
    outline: none;
    overflow: hidden;
    border-radius: 50px;
    font-weight: bold;
    cursor: pointer;
  }
  .form__button:hover {
    background: #e9cada;
    border: 2px solid #ff0081;
    transition: all 350ms cubic-bezier(0.215, 0.61, 0.355, 1);
  }
